"use client";

import { useEffect, useState } from "react";

export default function FormsPage() {
  const [forms, setForms] = useState([]);

  useEffect(() => {
    fetch("http://localhost:4000/api/forms", {
      credentials: "include",
    })
      .then(res => res.json())
      .then(data => setForms(data))
      .catch(() => {});
  }, []);

  return (
    <div>
      <h1>Forms</h1>

      {forms.length === 0 ? (
        <p>No forms found</p>
      ) : (
        <ul>
          {forms.map(f => (
            <li key={f._id}>
              <strong>{f.title}</strong>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
